export interface DoughnutChartTitleM {
    name: string;
    color: string;
}

export interface DoughnutChartDataM {
    metaData: Array<{
        name: string;
        value: number;
        unit: {
            name: string;
            code: string;
        }
    }>;
    toolbox: {
        showDataView: boolean;
        showRestore: boolean;
        showDownloadImage: boolean;
    };
}

export interface DoughnutChartM {
    title: DoughnutChartTitleM;
    data: DoughnutChartDataM;
}

export class DoughnutChart implements DoughnutChartM {
    title = {
        name: '',
        color: 'grey'
    };
    data = {
        metaData: [],
        toolbox: {
            showDataView: false,
            showRestore: false,
            showDownloadImage: false,
        },
    };
}